.NavBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  /*background-image: url('./background_search_desktop.jpg');*/
  /*background-size: cover;
  background-repeat: no-repeat;*/
  background-color: #6b727f;
  /*background-color: brown;*/
  /*height: 25rem;*/
  margin-bottom:  -100px;
  height:0px;
}

.NavBar-nav-options ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  /*background-color: blue;*/
  margin-bottom: 0.22rem;
  color: #000000;
  list-style-type: none;
  padding:0;
  /*height: 10px;*/
}

.NavBar-nav-options li {
	/*font-size: 20;*/
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0); 
  -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #6b727f;
  cursor: pointer;
  width: 100%;
  height: 20px;
  /*border-bottom: 3px solid #fff;*/
  border-color: white;
  padding: 0 2.58rem .33rem 2.58rem;
  /*padding:0;*/
  line-height: 1.13;
  margin-bottom: 0px;
  margin-top: -30px;
  text-align: center;
  font-weight: 600;
  font-size: 1.2rem;
  transition: color .25s;
}

.NavBar-nav-options li.active,
.NavBar-nav-options li.active:hover {
  border-bottom: 2px solid white;
  color: white;
}
